@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@200;400&family=Barlow:ital,wght@0,600;1,500&family=Bellefair&family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&family=Outfit:wght@700&family=Overpass:wght@300;600&family=PT+Sans&family=Roboto&family=Ubuntu:wght@400;500;700&display=swap');

:root {
  --lightRedGradient: hsl(353, 100%, 62%);
  --veryLightRedGradient: hsl(13, 100%, 72%);

  --veryDarkGrayBlueGradient: hsl(237, 17%, 21%);
  --veryDarkDesaturatedBlueGradient: hsl(237, 23%, 32%);

  --veryDarkBlue: hsl(208, 49%, 24%); /* for heading */
  --veryDarkGrayishBlue: hsl(207, 13%, 34%);

  --veryDarkBlackBlue: hsl(240, 10%, 16%);
  --veryLightRed: hsl(355, 100%, 74%);

  --grayishBlue: hsl(240, 2%, 79%);
}

.main {
  height: 95vh;
  width: 100%;
  background-image: linear-gradient(to left, var(--lightRedGradient), var(--veryLightRedGradient));
  border-bottom-left-radius: 100px;
}

.container {
  width: 100%;
  height: 100%;
  background: url("./components//Desktop/assets/bg-pattern-intro-desktop.svg") no-repeat;
  background-size: auto;
  background-position: 50%;
  background-position-x: 26.5%;
}

.navbar-container {
  display: flex;
  justify-content: space-around;
}

.navbar {
  display: flex;
  gap: 4rem;
  align-items: center;
  height: 130px;
}

.nav-list {
  display: inline-flex;
  gap: 2.5rem;
  margin: 0;
  padding: 0;
}

.nav-list-item {
  list-style: none;
  color: white;
}

.arrow-down {
  margin-inline-start: 10px;
}

.nav-list-item:hover {
  cursor: pointer;
}

.login-container {
  display: flex;
  gap: 30px;
  height: 130px;
  align-items: center;
}

a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.signup {
  padding: 13px 35px;
  background-color: white;
  color: var(--veryLightRedGradient);
  border-radius: 30px;
}

.signup:hover {
  background-color: var(--veryLightRed);
  color: white;
}

.main-content {
  text-align: center;
  margin-block-start: 7rem;
  color: white;
  font-family: 'Overpass', sans-serif;
}

.heading {
  font-weight: 600;
  font-size: 4rem;
  margin-block-end: 0;
}

.content {
  font-weight: 300;
  font-size: 1.3rem;
  margin-block-start: 1rem;
}

.free-more {
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  margin-block-start: 3rem;
}

.free {
  background-color: white;
  color: var(--lightRedGradient);
  padding: 10px 20px;
  border: 1px solid white;
  border-radius: 25px;
  font-weight: bold;
}

.free:hover {
  background-color: var(--veryLightRed);
  color: white;
  border-color: var(--veryLightRed);
}

.more {
  border: 1px solid white;
  padding: 10px 20px;
  border-radius: 25px;
}

.more:hover {
  background-color: white;
  color: var(--lightRedGradient);
}

/* Future Content Styling */

.future-background-container {
  width: 100%;
  height: 1000px;
}

.future-container {
  display: flex;
  gap: 160px;
}

.future-heading {
  font-family: 'Overpass', sans-serif;
  text-align: center;
  margin-block: 8rem;
  color: var(--veryDarkBlue);
  font-size: 40px;
  font-weight: 600;
}

.function-container {
  height: fit-content;
  width: 500px;
  margin-inline-start: 200px;
}

.function-flex {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.function-heading {
  color: var(--veryDarkBlue);
  font-family: "Overpass", sans-serif;
  font-weight: 600;
  font-size: 25px;
}

.function-description {
  color: var(--veryDarkGrayishBlue);
  font-family: "Overpass", sans-serif;
  font-weight: 300;
  font-size: 16px;
}

.illustration-image {
  margin-top: -250px;
}

.infrastructure-container {
  height: 400px;
  width: 100%;
  background-image: linear-gradient(to right, var(--veryDarkGrayBlueGradient), var(--veryDarkDesaturatedBlueGradient));
  margin-top: -50px;
  border-start-end-radius: 100px;
  border-end-start-radius: 100px;
}

.infra-content-container {
  background: url("./components/Desktop/assets/bg-pattern-circles.svg") no-repeat;
  background-size: auto;
  background-position-x: -42%;
  background-position-y: 80%;
  height: 100%;
  width: 100%;
  position: relative;
  color: white;
}

.mobile-illustration {
  position: absolute;
  left: 100px;
  top: -70px;
}

.simple-container {
  display: flex;
  margin-top: 150px;
  justify-content: center;
  align-items: center;
}

.laptop-illustration {
  margin-inline-start: -400px;
}

.footer {
  height: 400px;
  background-color: var(--veryDarkBlackBlue);
  border-start-end-radius: 100px;
  margin-top: 100px;
  position: relative;
}

.footer-logo {
  position: absolute;
  left: 150px;
  top: 100px;
}

.footer-content {
  display: flex;
  gap: 250px;
  color: white;
  position: absolute;
  top: 90px;
  left: 500px;
}

.footer-content-heading {
  margin-block-end: 50px;
}

.footer-content-item p:hover {
  cursor: pointer;
  text-decoration: underline;
}

.dropdown {
  position: relative;
}

.dropdown div {
  background-color: white;
  padding: 15px 70px 15px 20px;
  height: 0;
  border-radius: 5px;
  position: absolute;
  transition: height 1s;
}

.dropdown-1 {  
  left: 330px;
  top: -30px;
  width: 100px;
  visibility: hidden;
}

.dropdown-2 {
  left: 450px;
  top: -30px;
  width: 70px;
  visibility: hidden;
}

.dropdown-3 {
  left: 590px;
  top: -30px;
  width: 70px;
  visibility: hidden;
}

.dropdown div p:hover {
  cursor: pointer;
  font-weight: bold;
}

@media only screen and (max-width: 397px) {
  .App {
    overflow: hidden;
  }
  .main {
    background-image: linear-gradient(to top, var(--lightRedGradient), var(--veryLightRedGradient));
    width: 397px;
    height: 90vh;
    border-bottom-left-radius: 120px;
  }
  .container {
    background: url("./components/Desktop/assets/bg-pattern-intro-mobile.svg") no-repeat;
    background-size: 1600px;
    background-position-y: 35%;
    background-position-x: 35%;
    border-bottom-left-radius: 120px;
  }

  .navbar-container {
    width: 397px;
    height: 100px;
    display: flex;
    gap: 100px;
    justify-content: space-around;
  }

  .menu {
    height: 30px;
    width: 40px;
    position: relative;
    top: 50px;
  }

  .main-content {
    width: 100%;
    text-align: center;
  }

  .heading {
    font-size: 2.2rem;
  }

  .content {
    font-size: 1rem;
    width: 300px;
    text-align: center;
    margin-left: 45px;
    margin-bottom: 70px;
  }

  .future-heading {
    font-size: 30px;
  }

  .future-container {
    flex-direction: column-reverse;
    margin-top: 300px;
  }

  .function-heading {
    width: 200px;
    text-align: center;
    margin-inline-start: 50px;
  }

  .function-description {
    margin-block-end: 70px;
    line-height: 25px;
  }

  .function-container {
    margin-inline-start: 0px;
    margin-block-start: -100px;
    padding: 0px 50px;
    width: 300px;
    text-align: center;
  }

  .infrastructure-container {
    height: 800px;
    margin-block-start: 300px;
    width: 397px;
  }

  .mobile-illustration {
    left: -60px;
    top: -250px;
    scale: 0.8;
    width: 525px;
  }

  .simple-container {
    flex-direction: column;
    margin-block-start: 70px;
  }

  .laptop-illustration {
    margin-block-end: 150px;
    margin-inline-start: 0px;
  }

  .footer {
    height: 1100px;
    width: 100%;
  }

  .footer-content {
    flex-direction: column;
    left: 150px;
    top: 200px;
    gap: 70px;
  }

  .footer-content-item {
    text-align: center;
  }

  .infra-content-container {
    background-position-x: 30%;
    background-position-y: 200%;
    background-size: auto;
    border-top-right-radius: 100px;
  }

  .menu-container {
    width: 330px;
    margin: 30px;
    padding-block: 20px;
    background-color: white;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    position: relative;
    bottom: 370px;
  }

  .menu-title {
    margin: 0;
  }

  .dropdown-1, .dropdown-2, .dropdown-3 {
    visibility: visible;
    width: 90%;
    text-align: center;
    background-color: var(--grayishBlue);
    border-radius: 7px;
  }
  .menu-break-line {
    color: black;
    width: 80%;
    margin-top: -20px;
  }

  .mobile-login-anchor {
    text-decoration: none;
    color: black;
    font-size: 18px;
    margin: 0;
    margin-top: -20px;
  }

  .mobile-signup-anchor {
    background-image: linear-gradient(to left, var(--lightRedGradient), var(--veryLightRedGradient));
    padding: 15px 30px;
    border-radius: 25px;
  }
}